import React from 'react';
import { Gitgraph, templateExtend, TemplateName } from '@gitgraph/react';

const customTemplate = templateExtend(TemplateName.Metro, {
  commit: {
    message: {
      displayAuthor: false,
      displayHash: false,
    },
  },
});

const ExperienceGraph = ({ experience }) => {
  const reversedExperience = [...experience].reverse();

  return (
    <Gitgraph
      options={{
        template: customTemplate,
      }}
    >
      {(gitgraph) => {
        const master = gitgraph.branch('master');

        reversedExperience.forEach((exp, i) => {
          master.commit({
            subject: `Job ${experience.length - i}`,
            renderDot: (commit) => (
              <g>
                <foreignObject x={commit.x + 15} y={commit.y - 50} width="200" height="100">
                  <div xmlns="http://www.w3.org/1999/xhtml" style={{backgroundColor: "#f8f9fa", padding: "10px", borderRadius: "5px", boxShadow: "0 0.5rem 1rem rgba(0, 0, 0, 0.15)"}}>
                    <h3 style={{marginBottom: "0"}}>{exp.company}</h3>
                    <small className="text-muted">{exp.position}</small>
                    <hr style={{margin: "5px 0"}}/>
                    <ul style={{listStyle: "none", paddingLeft: "0", margin: "0"}}>
                      <li>{exp.from} - {exp.to}</li>
                      <li>{exp.year}</li>
                    </ul>
                  </div>
                </foreignObject>
              </g>
            ),
          });
        });
      }}
    </Gitgraph>
  );
};

export default ExperienceGraph;