import React from "react";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";
import ExperienceGraph from "./ExperienceGraph";

const Resume = () => {
  const experience = [
    { company: "Company 1", position: "Position 1", from: "2010", to: "2015", year: "5 years" },
    { company: "Company 2", position: "Position 2", from: "2016", to: "2018", year: "2 years" },
    { company: "Company 3", position: "Position 3", from: "2019", to: "Present", year: "4 years" },
    // Add as many experiences as needed...
  ];

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-6">
          {experience.map((exp, index) => (
            <Card key={index} style={{ width: '100%', marginTop: '1rem' }}>
              <Card.Body>
                <Card.Title>{exp.company}</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">{exp.position}</Card.Subtitle>
                <ListGroup variant="flush">
                  <ListGroup.Item>{exp.from} - {exp.to}</ListGroup.Item>
                  <ListGroup.Item>{exp.year}</ListGroup.Item>
                </ListGroup>
              </Card.Body>
            </Card>
          ))}
        </div>
        <div className="col-md-6">
          <ExperienceGraph experience={experience} />
        </div>
      </div>
    </div>
  );
}

export default Resume;
